  background-repeat: no-repeat;
  display: block;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, 0);
  width: 50px;
  height: 30px;
}
.creator__video__edit {
  position: absolute;
  right: 15px;
  top: 10px;
  z-index: 1;
}
